import React, { Component } from "react";

import './AddToDo.css';

export default class AddTodo extends Component {

  state = {
    Id: +this.props.todos[this.props.todos.length - 1]['Id'] + 1,
    Title: "",
    Status: ""
  };
  handleTitleChange = (event) => {
    this.setState({
      Title: event.target.value
    });
  }
  handleStatusChange = (event) => {
    this.setState({
      Status: event.target.value
    });
  };
  handleToDoSubmit = (event) => {
    event.preventDefault();
    this.props.onAdd({
      Id: this.state.Id,
      Title: this.state.Title ? this.state.Title : "None",
      Status: this.state.Status ? this.state.Status : "Pending"
    });
    this.setState({
      Id: this.state.Id + 1,
      Title: "",
      Status: ""
    });
  };
  render() {
    return (
      <div>
        <h1 className="header">
          Add ToDo
                </h1>
        <form onSubmit={this.handleToDoSubmit} >
          <div className="form-group" >
            <input value={"ID: " + this.state.Id} className="form-control" placeholder="Enter Id" readonly="readonly" />
          </div>
          <div className="form-group" >
            <input value={this.state.Title} onChange={this.handleTitleChange} className="form-control" placeholder="Enter Title" />
          </div>
          <div className="form-group">
            <select value={this.state.Status} onChange={this.handleStatusChange} className="select-status form-control" >
              <option value="" disabled selected>Select your status</option>
              <option value="Done" >Done</option>
              <option value="Pending" >Pending</option>
            </select>
          </div>
          <button type="submit" className="form-control btn btn-primary" >Add Todo</button>
        </form>
      </div>
    );
  }
}